import {
  View,
  Text,
  ScrollView,
  StyleSheet,
  TouchableOpacity,
  LayoutChangeEvent,
} from "react-native";
import { useEffect, useState, useRef } from "react";
import { TMDB_API_KEY } from "../constants";
import CastMemberCard from "./CastMemberCard";
import CastMember from "../types/CastMember";
import { Ionicons } from "@expo/vector-icons";

function Credits({
  id,
  mediaType,
}: {
  id: number;
  mediaType: "movie" | "tv";
}) {
  const [cast, setCast] = useState<CastMember[]>([]);
  const scrollRef = useRef<ScrollView>(null);
  const scrollPosition = useRef(0);
  const [contentWidth, setContentWidth] = useState(0);
  const [scrollViewWidth, setScrollViewWidth] = useState(0);

  useEffect(() => {
    const controller = new AbortController();

    fetch(
      `https://api.themoviedb.org/3/${mediaType}/${id}/credits?api_key=${TMDB_API_KEY}`,
      { method: "GET", signal: controller.signal }
    )
      .then((res) => res.json())
      .then((data) => {
        const sorted = (data?.cast || []).sort((a, b) => a.order - b.order);
        setCast(sorted.slice(0, 12)); // Top 12 actors
      })
      .catch(console.error);

    return () => controller.abort();
  }, [id, mediaType]);

  const scrollBy = (direction: "left" | "right") => {
    const offset = 150;

    scrollPosition.current += direction === "left" ? -offset : offset;

    if (scrollPosition.current < 0) scrollPosition.current = 0;

    const maxScroll = contentWidth - scrollViewWidth;
    if (scrollPosition.current > maxScroll) {
      scrollPosition.current = maxScroll;
    }

    scrollRef.current?.scrollTo({
      x: scrollPosition.current,
      animated: true,
    });
  };

  return (
    <View style={styles.container}>
      <Text style={styles.title}>Starring:</Text>
      <View style={styles.scrollWrapper}>
        <TouchableOpacity onPress={() => scrollBy("left")} style={styles.arrow}>
          <Ionicons name="chevron-back" size={24} color="#FFF" />
        </TouchableOpacity>

        <ScrollView
          horizontal
          ref={scrollRef}
          showsHorizontalScrollIndicator={false}
          contentContainerStyle={styles.castList}
          style={styles.scroll}
          onContentSizeChange={(w) => setContentWidth(w)}
          onLayout={(e: LayoutChangeEvent) =>
            setScrollViewWidth(e.nativeEvent.layout.width)
          }
        >
          {cast.map((member) => (
            <CastMemberCard key={member.id} castMember={member} />
          ))}
        </ScrollView>

        <TouchableOpacity onPress={() => scrollBy("right")} style={styles.arrow}>
          <Ionicons name="chevron-forward" size={24} color="#FFF" />
        </TouchableOpacity>
      </View>
    </View>
  );
}

const styles = StyleSheet.create({
  container: {
    marginTop: 8,
  },
  title: {
    color: "#FFF",
    fontWeight: "bold",
    fontSize: 18,
    marginLeft: 12,
    marginBottom: 6,
  },
  scrollWrapper: {
    flexDirection: "row",
    alignItems: "center",
  },
  arrow: {
    padding: 6,
    zIndex: 1,
  },
  castList: {
    paddingHorizontal: 8,
    alignItems: "center",
  },
  scroll: {
    flex: 1,
    height: 150,
  },
});

export default Credits;


